.single-Entry {
  position: relative;
  width: 100%;
  margin: {
    top: 2.8em;
  }
  color: $textDefaultColor;
  font-size: 1.6rem;
  letter-spacing: .06em;
  line-height: 1.7;
  word-break: break-all;
  text-align: justify;

  @include for-tablet-portrait-up {
    font-size: 1.6rem + 0.2;
    line-height: 2;
    text-align: left;
  }

  @include for-tablet-landscape-up {
    font-size: 1.6rem + 0.2 + 0.2;
  }

  @include for-desktop-up {
    font-size: 2.0rem;
  }

  & > * {
    width: 90%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 85%;
    }

    @include for-tablet-landscape-up {
      width: 80%;
    }

    @include for-desktop-up {
      max-width: 960px - 200px;
    }

  }

  h2 {
    position: relative;
    margin: {
      top: 6em;
      bottom: .6666em;
    }
    font-weight: bold;
    font-size: 1.8rem;
    letter-spacing: .2em;

    @include for-tablet-portrait-up {
      font-size: 1.8rem + 0.3;
    }

    @include for-tablet-landscape-up {
      font-size: 1.8rem + 0.3 + 0.3;
    }

    @include for-desktop-up {
      font-size: 2.4rem;
    }

    &::before {
      position: absolute;
      content: '*';
      display: block;
      top: -3.8em;
      width: 100%;
      text-align: center;
    }

    &:nth-child(1){
      margin: {
        top: 0;
      }

      &::before {
        display: none;
      }

    }

  }

  h3 {
    margin: {
      top: 1.2em;
      bottom: .4em;
    }
    font-weight: bold;

  }

  & > p,
  & > pre {
    margin: {
      bottom: 1.2em;
    }

  }

  & > p code {
    background-color: #efefef;
    padding: {
      right: .5em;
      left: .5em;
    }
  }

  p > a {
    margin: {
      right: .2em;
      left: .2em;
    }

    &:link,
    &:visited {
      color: $linkColor;
      text-decoration: none;
      transition: all $transitionSpeed;
    }

    &:hover:link,
    &:hover:visited {
      @include for-tablet-portrait-up {
        background-color: rgba($linkColor, .2);
      }
    }

  }

  & > ul,
  & > ol {
    margin: {
      bottom: .4em;
    }
    padding: {
      left: 2em;
    }

    li {
      list-style-type: inherit;
    }

  }

  blockquote {
    position: relative;
    width: 100%;
    max-width: none;
    margin: {
      bottom: .8em;
    }
    background-color: #efefef;
    color: #555;
    font-style: italic;


    &::before{
      z-index: 1;
      position: absolute;
      top: .16em;
      left: 50%;
      content: "\f10d";
      display: inline-block;
      width: 90%;
      color: #cfcfcf;
      font-family: "Font Awesome 5 Free";
      font-size: 5rem;
      line-height: 1;
      opacity: .6;
      transform: translateX(-50%);

      @include for-tablet-portrait-up {
        width: 85%;
      }

      @include for-tablet-landscape-up {
        width: 80%;
      }

      @include for-desktop-up {
        max-width: 960px - 200px;
      }

    }

    p {
      position: relative;
      z-index: 2;
      width: 90%;
      margin: {
        top: 0;
        right: auto;
        bottom: 0;
        left: auto;
      }
      padding: {
        top: 1.6em;
        right: 2em;
        bottom: 1.2em;
        left: 2em;
      }

      @include for-tablet-portrait-up {
        width: 85%;
      }

      @include for-tablet-landscape-up {
        width: 80%;
      }

      @include for-desktop-up {
        max-width: 960px - 200px;
      }

    }

    // cite {
    //   display: block;
    //   text-align: right;
    //   color: #888888;
    //   font-size: 0.9em;
    // }

  }

  img {
    width: 100%;
    height: auto;
  }

  & > #{&}_ImgLarge {
    @include for-tablet-landscape-up {
      width: 90%;
    }

    @include for-desktop-up {
      max-width: 960px;
    }

  }

  &_Ytube {
    margin: {
      bottom: 1.5em;
    }
    @include for-desktop-up {
      margin: {
        bottom: 24px;
      }
    }
  }

  &_YtubeInner {
    position:relative;
    width: 100%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }
    padding: {
      top: 56.25%;
    }
    text-align: center;

    iframe {
      position:absolute;
      top:0;
      right:0;
      width:100%;
      height:100%;
    }

  }

  &_YtubeOther {
    margin: {
      bottom: .8em;
    }

    iframe {
      width: 100%;
    }

  }


}